.navbar:hover .ea-icon-logo{-webkit-transform:rotate(0deg);transform:rotate(0deg);transition:600ms ease}}.navbar .link-text{display:none;opacity:0;transition:600ms ease;font-size:1.6rem;margin-left:1rem}.navbar .logo{display:block;background-color:#141718}@media (max-width: 600px){.navbar .logo{display:none}}.navbar .logo-text{display:inline;position:absolute;left:-999px}.navbar .logo-text h1{font-weight:bold;text-transform:uppercase;text-align:center;color:#b2c2e2;background:#141718;font-size:2.2rem;letter-spacing:0.3ch;width:100%}.navbar .ea-icon-logo{min-width:5rem;margin:0 1.2rem;-webkit-transform:rotate(180deg);transform:rotate(180deg);transition:600ms ease}.navbar .navbar-nav{list-style:none;padding:0;margin:0;display:flex;flex-direction:column;align-items:center;height:100%}@media (max-width: 600px){.navbar .navbar-nav{flex-direction:row;justify-content:space-evenly}}.navbar .navbar-nav .nav-item{width:100%}@media (max-width: 600px){.navbar .navbar-nav .nav-item{width:8rem}}.navbar .navbar-nav .nav-item:last-child{margin-top:auto}.navbar .navbar-nav .nav-item .nav-link{display:flex;align-items:center;height:8rem;color:#b2c2e2;text-decoration:none;-webkit-filter:grayscale(100%) opacity(0.7);filter:grayscale(100%) opacity(0.7);transition:600ms ease}.navbar .navbar-nav .nav-item .nav-link:hover{-webkit-filter:grayscale(0%) opacity(1);filter:grayscale(0%) opacity(1);background:rgba(20,23,24,0.5);color:#ddd}@media (max-width: 600px){.navbar .navbar-nav .nav-item .nav-link .link-text{font-size:2rem;opacity:0;transition:600ms ease}.navbar .navbar-nav .nav-item .nav-link:hover .link-text{margin-left:-8rem;opacity:1;transition:600ms ease}}.navbar .navbar-nav .nav-item .nav-link .ea-base-logo{color:#bd3b2a}.navbar .navbar-nav .nav-item .nav-link .ea-shine-logo{color:#6a2117}.navbar .navbar-nav .nav-item .nav-link .ea-icon{min-width:4rem;margin:0 2rem}@media (max-width: 600px){.navbar .navbar-nav .nav-item .nav-link .ea-icon{min-width:6rem;margin-left:1rem}}.navbar .navbar-nav .nav-item .nav-link .ea-icon .ea-base{color:#4fa1c2}.navbar .navbar-nav .nav-item .nav-link .ea-icon .ea-shine{color:#9bc9dc}.project-card{margin:3rem;border:1.6rem solid transparent;border-image:url(/static/media/project-border-bg.84517452.svg) 15% stretch;box-shadow:3px 4px 12px 6px #123}.project-item{background-color:#abc;display:flex;align-items:stretch;flex-direction:row;padding:.5rem}@media (max-width: 600px){.project-item{flex-direction:column}}.project-item h1{padding:0 0 1rem;color:#123}.project-item p{color:#123}.header{margin:1rem 0 0 3rem;font-size:5rem;color:#b4b4c9}.project-img{background-color:#abc;padding:2rem;margin:1rem;min-width:8rem;min-height:8rem;background-position-x:-40px;background-position-y:-80px;background-size:800px;transition:3s;border:1rem solid transparent;border-image:url(/static/media/border-inner.aea1072f.svg) 25% stretch}.project-img:hover{transition:3s;background-position-x:-10px;background-position-y:-60px;background-size:480px}@media (max-width: 600px){.project-img{height:16rem}}.project-icons{height:5rem;display:flex;align-items:center;justify-content:space-around}@media (max-width: 600px){.project-icons{justify-content:space-between}}.project-icons img{width:4rem;padding-right:0rem}.project{margin:1rem}.project a{text-decoration:none;color:#2b5580}.project p{padding-top:1rem;max-width:60rem;line-height:2.3rem}@media (max-width: 600px){.project p{width:30rem}}.main{font-size:1.8rem;margin-top:0;margin-left:8rem;display:flex;justify-content:space-around;align-items:flex-start}@media (max-width: 600px){.main{margin:1rem}}.main .splash{display:flex;flex-direction:row-reverse;align-items:center;justify-content:space-around;font-family:"Josefin Sans",sans-serif;width:100%;margin:2rem;padding:5rem;border:2rem solid transparent;border-image:url(/static/media/border-light.f46a75ce.svg) 15% stretch;background-color:#abc;border-radius:5rem;min-width:60vw;box-shadow:7px 10px 23px -6px rgba(0,0,0,0.75)}@media (max-width: 800px){.main .splash{justify-content:flex-start;flex-direction:column-reverse}}.main .splash .logo-text{z-index:0;-webkit-transform:rotate(-5deg);transform:rotate(-5deg);width:28rem;height:10rem;background-image:url(/static/media/logo-backing.c5e791e9.svg);background-size:cover}.main .splash h1{padding:4rem 0 0 3rem;font-size:3.8rem;color:#abc}.main .splash h2{padding-left:2rem;padding-top:2rem;color:#234}.main .splash .profile-picture{margin:3rem;border:2rem solid transparent;border-image:url(/static/media/border-profile.657665f5.svg) 15% stretch}.main .splash .profile{display:flex;justify-content:space-around;align-items:flex-end;background-image:radial-gradient(ellipse at top, rgba(17,34,51,0.45), transparent),linear-gradient(transparent, rgba(17,34,51,0.4))}.main .splash .profile .profile-pic{transition:10s;-webkit-filter:grayscale(100%);filter:grayscale(100%);min-width:18rem;height:22rem}.main .splash .profile .profile-pic:hover{transition:3s;-webkit-filter:grayscale(30%);filter:grayscale(30%)}@media (max-width: 800px){.main .splash .profile .profile-pic{height:25rem}}.main .splash .profile:hover{background-image:radial-gradient(ellipse at top, rgba(179,0,179,0.6), transparent),linear-gradient(transparent, rgba(30,60,89,0.5))}.skills{padding:1rem 1rem 0 3rem;width:85vw}.skills h1{font-size:5rem;color:#b4b4c9}.skills .skill-container{border:3rem solid #abc;border-image:url(/static/media/border-light.f46a75ce.svg) 25% stretch}.skills .skill-content{color:#123;padding:1rem;background-color:#abc}footer{margin-top:auto;width:100%;display:flex;justify-content:flex-end;font-size:1.6rem;color:#b4b4c9}@media (max-width: 600px){footer{justify-content:space-around;padding-bottom:10rem}}footer p{padding-right:5vw}

